import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules } from '@angular/router';
import {HomeComponent} from './home/home.component';
import {RequestNotFoundComponent} from './request-not-found/request-not-found.component';
import {NoAccessComponent} from './no-access/no-access.component'; 
import {ProductTypeFormComponent} from './product-types/product-type-form/product-type-form.component';
import {ProductTypesComponent} from './product-types/product-types.component';
import {ProductFormComponent} from './products/product-form/product-form.component'; 
import {ProductsComponent} from './products/products.component';
import {ProductViewComponent} from './products/product-view/product-view.component';
import {AudiancesComponent} from './audiances/audiances.component';
import {AudianceFormComponent} from './audiances/audiance-form/audiance-form.component';
import {PreRequisitesComponent} from './pre-requisites/pre-requisites.component';
import {SkillsComponent} from './skills/skills.component';
import {SkillFormComponent} from './skills/skill-form/skill-form.component';
import {TopicsComponent} from './topics/topics.component';
import {TopicFormComponent} from './topics/topic-form/topic-form.component';
import {DistributionMethodsComponent} from './distribution-methods/distribution-methods.component';
import {DistributionMethodFormComponent} from './distribution-methods/distribution-method-form/distribution-method-form.component';
import {TopicViewComponent} from './topics/topic-view/topic-view.component';
import {DistributionMethodViewComponent} from './distribution-methods/distribution-method-view/distribution-method-view.component';
import {AudianceViewComponent} from './audiances/audiance-view/audiance-view.component';
import {SkillViewComponent} from './skills/skill-view/skill-view.component';
import {PreRequisiteFormComponent} from './pre-requisites/pre-requisite-form/pre-requisite-form.component';
import {PreRequisiteViewComponent} from './pre-requisites/pre-requisite-view/pre-requisite-view.component';
import {ServiceCategoryComponent} from './service-category/service-category.component';
import {ServiceCategoryViewComponent} from './service-category/service-category-view/service-category-view.component';
import {ServiceCategoryFormComponent} from './service-category/service-category-form/service-category-form.component';


const appRoutes: Routes = [
            {path: '', component: HomeComponent},
           // {path: 'accounts', component: AccountComponent},
            
            /* Product */
            {path: 'products', component: ProductsComponent},
            {path: 'products/:id/view', component: ProductViewComponent},
            {path: 'admin/products/new', component: ProductFormComponent},
            {path: 'admin/products/:id', component: ProductFormComponent},
            
            /* Product Type */
            {path: 'producttypes', component: ProductTypesComponent},
            {path: 'producttypes/:id/view', component: ProductViewComponent},
            {path: 'admin/producttypes/new', component: ProductTypeFormComponent},
            {path: 'admin/producttypes/:id', component: ProductTypeFormComponent},
            
            /* Pre Requisites */
            {path: 'prerequisites', component: PreRequisitesComponent},
            {path: 'prerequisites/:id/view', component: PreRequisiteViewComponent},
            {path: 'admin/prerequisites/new', component: PreRequisiteFormComponent},
            {path: 'admin/prerequisites/:id', component: PreRequisiteFormComponent},
            
            /* Skills */
            {path: 'skills', component: SkillsComponent},
            {path: 'skills/:id/view', component: SkillViewComponent},
            {path: 'admin/skills/new', component: SkillFormComponent},
            {path: 'admin/skills/:id', component: SkillFormComponent},
            
            /* Audiances */
            {path: 'audiances', component: AudiancesComponent},
            {path: 'audiances/:id/view', component: AudianceViewComponent},
            {path: 'admin/audiances/new', component: AudianceFormComponent},
            {path: 'admin/audiances/:id', component: AudianceFormComponent},
            
            /* Topics */
            {path: 'topics', component: TopicsComponent},
            {path: 'topics/:id/view', component: TopicViewComponent},
            {path: 'admin/topics/new', component: TopicFormComponent},
            {path: 'admin/topics/:id', component: TopicFormComponent},
            
            /* Distribution Methods */
            {path: 'distributionmethods', component: DistributionMethodsComponent},
            {path: 'distributionmethods/:id/view', component: DistributionMethodViewComponent},
            {path: 'admin/distributionmethods/new', component: DistributionMethodFormComponent},
            {path: 'admin/distributionmethods/:id', component: DistributionMethodFormComponent},
            
            /* Service Categories */
            {path: 'servicecategories', component: ServiceCategoryComponent},
            {path: 'services/:name', component: ServiceCategoryViewComponent},
            {path: 'admin/servicecategories/new', component: ServiceCategoryFormComponent},
            {path: 'admin/servicecategories/:id', component: ServiceCategoryFormComponent},
            
            
          //  {path: 'admin', component: AdminComponent},
          //  {path: 'login', component: LoginComponent},
            {path: 'no-access', component: NoAccessComponent},
            {path: '**', component: RequestNotFoundComponent}
        ];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes, {preloadingStrategy: PreloadAllModules})],
  exports: [RouterModule]
})

export class AppRoutingModule { }



