import { Component, OnInit } from '@angular/core';  
import {ActivatedRoute} from '@angular/router';
import {ServiceCategory} from '../../common/models/service-category';
import {ServiceCategoryService} from '../service-category.service';

@Component({
  selector: 'app-service-category-view',
  templateUrl: './service-category-view.component.html',
  styleUrls: ['./service-category-view.component.css']
})
export class ServiceCategoryViewComponent implements OnInit {

  serviceCategoryName: string ;  
    serviceCategory: ServiceCategory;  

    constructor(private _serviceCategoryService: ServiceCategoryService, 
        private route: ActivatedRoute ) { 
        
        this.serviceCategoryName = this.route.snapshot.paramMap.get('name'); 
 
        this.serviceCategory = {
            name: "",
            shortName: "",
            imageMetadata: {filename: ""},
            thumbnailMetadata: {filename: ""}
        };

        if (this.serviceCategoryName) {
            this.getServiceCategoryByShortName(this.serviceCategoryName);
        } 
    }
  
  ngOnInit() {
  }
  
    getServiceCategoryByShortName(shortName: String) {
        this._serviceCategoryService.getByShortName(shortName).subscribe(sc => {
            this.serviceCategory = sc;
           // this.loadServiceCategoryImages(this.serviceCategory);
        });
    }
 
}



