import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/retry';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

import {NotFoundError} from '../not-found-error';
import {BadInputError} from '../bad-input-error';
import {AppError} from '../app-error';
import {UniversalDeclerations} from '../universal-declerations';

@Injectable()
export class DataService extends UniversalDeclerations {
    headers: HttpHeaders;
    //private headers: HttpHeaders;
    private url: string

    constructor(private httpClient: HttpClient, resourcePath: string) {
        super();
        this.url = this.hostUrl + resourcePath;
        this.httpClient = httpClient;
        //alert (this.url) ;        

        this.headers = new HttpHeaders();
        this.headers = this.headers.set('Content-Type', 'application/json; charset=utf-8');
    }

    getAll() {
        return this.httpClient.get(this.url)
            .retry(3)
            .catch(this.handleError);
    }

    create(resource: any) {
        return this.httpClient.post(this.url, JSON.stringify(resource), {
            headers: this.headers
        })
            .catch(this.handleError);
    }

    get(id: any) {
        return this.httpClient.get(this.url + '/' + id)
            .retry(3)
            .catch(this.handleError);
    }
    
    getByShortName(shortName : String){
        return this.httpClient.get(this.url + '/getByShortName/' + shortName)
            .retry(3)
            .catch(this.handleError);
            }

    update(id: any, resource: any) {
        //alert('headers : ' + JSON.stringify(this.headers));
        //alert('Update.\n' + this.url + '/' + id + "\n\n" + JSON.stringify(resource));
        return this.httpClient.put(this.url + '/' + id, JSON.stringify(resource), {
            headers: this.headers
        })
            .retry(3)
            .catch(this.handleError);
    }

    delete(resource: HTMLInputElement) {
        // alert('resource.id : ' + resource.id);
        return this.httpClient.delete(this.url + '/' + resource.id)
            //.retry(3)
            .catch(this.handleError);
    }

    getByProperty(propertyName: string, propertyValue: any) {
        return this.httpClient.get(this.url + '/getBy' + propertyName + '/' + propertyValue)
            .retry(3)
            .catch(this.handleError);
    }

    private handleError(error: Response) {
        if (error.status === 404)
            return Observable.throw(new NotFoundError());

        else if (error.status === 400)
            return Observable.throw(new BadInputError(error));

        else return Observable.throw(new AppError(error));
    }
}



