import { Component, OnInit } from '@angular/core';
import {AppError} from '../common/app-error';
import {NotFoundError} from '../common/not-found-error';
import {VendorService} from './vendor.service'; 
import {Vendor} from '../common/models/vendor';
@Component({
  selector: 'app-vendors',
  templateUrl: './vendors.component.html',
  styleUrls: ['./vendors.component.css']
})
export class VendorsComponent implements OnInit {

     vendors: Vendor[]; 

    constructor(private _vendorService: VendorService ) {}

    ngOnInit() {
        this.getVendors(); 
    } 

    getVendors  () {
        this._vendorService.getAll().subscribe(prods => this.vendors = prods);
    }

    deleteVendor(vendor) {

        this._vendorService.delete(vendor)
            .subscribe( () => {
                const index = this.vendors.indexOf(vendor);
                this.vendors.splice(index, 1);
            },
            (error: AppError) => {
                if (error instanceof NotFoundError)
                    alert('vendor has already been deleted.');
                else throw error;
            }); 
    }

}



